.App {
margin-left: 40%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

div, button {
  margin:15px;
}

.Artist{
  margin-top: -60%;
  margin-left: 40%;
}

.Tracks{
  background-image: linear-gradient(to right, red, black);
  margin-left:-35%;
  color: white;
  text-align: center;
}

.Artists{
  margin-left: -35%;
  text-align: center;
  background-image: linear-gradient(to right, red, black);
  color: white;
}
